import React, { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import SweetAlert2 from "react-sweetalert2";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { decrypt } from '../utils/cryptoUtils';
import {
  getAllMultipliers,
  getBigWinnerList,
  getLastGamePlanetswing,
  getTodayBonus,
  getUserDetailApi,
  newGameStart,
  placeBet,
} from "../api/api";
import BetForm from "../components/BetForm";
import CurrentBetSection from "../components/CurrentBetSection";
import Footer from "../components/Footer";
import Header from "../components/Header";
import Modal from "../components/Modal";
import PlanetsSection from "../components/PlanetsSection/index";
import RecentResultSection from "../components/RecentResultSection";
import ResultModalContent from "../components/ResultModalContent";
import TodaysRoundsSection from "../components/TodaysRoundsSection";
import GameTimer from "../components/GameTimer";

type Params = {
  id: string;
};

interface WebSocketMessage {
  type: string;
  payload: any;
}

const getTimestampInSeconds = () => {
  let currentSecond = new Date().getSeconds();
  if (currentSecond > 30) {
    currentSecond = currentSecond - 30;
  }

  return currentSecond;
};

const HomePage: React.FC = () => {
  const videoRef = useRef<HTMLVideoElement | null>(null);
  const { encryptedId } = useParams<{ encryptedId: string }>();
	const [userId, setUserId] = useState<number>(0);

  const [planet, setPlanet] = useState([]);
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const [showPlanets, setShowPlanets] = useState<boolean>(true);
  const [selectedValue, setSelectedValue] = useState<string>("");
  const [selectPlanet, setSelectPlanet] = useState<Number>(0);
  const [childValue, setChildValue] = useState<boolean>(false);
  const [endGame, setEndGame] = useState<boolean>(false);
  const [endGameDetail, setEndGameDetail] = useState<any>({});
  const [winAmount, setWinAmount] = useState<number>(0);
  const [userBetOnPlants, setUserBetOnPlants] = useState<any>([]);
  const [gameId, setGameId] = useState<number>(0);
  const [swalProps, setSwalProps] = useState({});
  const [savedTime, setSavedTime] = useState(0);
  const [userDetail, setUserDetail] = useState<any>({});
  const [accountBal, setAccountBal] = useState<number>(0);
  const [todayBonus, setTodayBonus] = useState<any>({});
  const [bigWinnerList, setBigWinnerList] = useState<BigWinnerItem[]>([]);
  const [betOnPlanet, setBetOnPlanet] = useState<[]>([]);
  const [betAmount, setBetAmount] = useState<number>(0);
  const [enterAmount, setEnterAmount] = useState<string>("");
  const [lastWingPlanetList, setLastWingPlanetList] = useState<
    LastWingPlanets[]
  >([]);

  const [valid, setValid] = useState<boolean>(false);

  //Socket Code
  const [message, setMessage] = useState<string>("");
  const [socket, setSocket] = useState<WebSocket | null>(null);

  useEffect(() => {
    if (!encryptedId) return;
    if (encryptedId) {
      const decrypted = decrypt(encryptedId);	
      let id = parseInt(decrypted);
      setUserId(id);
      
    fetchData();
    fetchUserDetail();
    fetchTodayBonus();
    fetchBigWinnerList();
    if (gameId == 0) {
      startGame();
    }
    let second = getTimestampInSeconds();
    // console.log("se",localStorage.getItem("timeLeft"));
    // setSavedTime(second);
    localStorage.setItem("timeLeft", second.toString());
    // if(savedTime == null){
    // console.log("saveTime",savedTime);

    // }
    }
  }, [encryptedId,userId]);

  useEffect(() => {
    fetchUserDetail();
    fetchTodayBonus();
  }, [endGame]);

  const sendMessage = () => {
    if (socket && socket.readyState === WebSocket.OPEN) {
      const messageToSend: WebSocketMessage = {
        type: "clientMessage",
        payload: "Client Message",
      };
      socket.send(JSON.stringify(messageToSend));
    }
  };

  const handleChildValue = (value: boolean) => {
    setChildValue(value);
  };

  const handleEndGameFlag = (value: boolean) => {
    setEndGame(value);
    setBetOnPlanet([]);
    setBetAmount(0);
  };

  const handleAlreadyGameStart = (value: any) => {
    if (value != "") {
      setGameId(value);
      setUserBetOnPlants([]);
      setEndGame(false);
      fetchLastGameWingPlanets();
    }
  };

  const handleUserBits = (value: any) => {
    setUserBetOnPlants([]);
  };

  async function fetchData() {
    const data = await getAllMultipliers();
    if (data.status) {
      setPlanet(data.details);
    } else {
      setPlanet([]);
    }
  }

  async function fetchUserDetail() {
    console.log("User Id Fatch User Detail",userId);
 	
	 	if(userId > 0){
    const param = {
      userId:userId,
    };
      const data = await getUserDetailApi(param);
      if (data.status == 1) {
        let coins = data?.details?.coins;
        setUserDetail(data.details);
        setAccountBal(coins);
      }
    }
  }

  async function fetchTodayBonus() {
    const param = {
      userId:userId,
    };
    const data = await getTodayBonus(param);

    if (data.success == 1) {
      setTodayBonus(data.data);
    }
  }

  async function addBets(param: any) {
    const data = await placeBet(param);

    if (data.status == 1) {
      setUserBetOnPlants([...userBetOnPlants, param.planetId]);
      fetchUserDetail();
      setBetOnPlanet(data.bet_on);
      setBetAmount(data.total_bet);
      setAccountBal(data.account_amount);
    } else {
    }
  }

  async function startGame() {
    const data = await newGameStart();
    setGameId(data.game_id);
    setUserBetOnPlants([]);
    fetchLastGameWingPlanets();
  }

  async function fetchBigWinnerList() {
    const data = await getBigWinnerList();
    if (data.success == 1) {
      setBigWinnerList(data.data);
    }
  }

  async function fetchLastGameWingPlanets() {
    const data = await getLastGamePlanetswing();

    if (data.status == "1") {
      setLastWingPlanetList(data.data);
    }
  }

  // Toggle the popup state
  const togglePopup = (value: number) => {
    setSelectPlanet(value);
    setIsOpen(!isOpen);
  };

  // Handler for radio button change
  const handleRadioChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if(parseInt(event.target.value) <50000 ){
      setValid(false);
    }else{
      setValid(true);
      }
    setSelectedValue(event.target.value);
    setEnterAmount(event.target.value);
  };
  const handleEndGameDetail = (value: any) => {
    setEndGameDetail(value);
    setSelectedValue("");
    setEnterAmount("");
  };
  const handleWingAmount = (value: number) => {
    setWinAmount(value);
  };
  // Handler for form submission
  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();

    if (!enterAmount || parseInt(enterAmount) > 50000 ) {
      setValid(true);
        return;
      }
      setValid(false);

    if (childValue) {
      togglePopup(0);
      return;
    }
    // Data to send to the API
    const data = {
      userId: userId,
      amount: enterAmount,
      planetId: selectPlanet,
    };
    sendMessage();
    try {
      addBets(data);
      togglePopup(0);
      // setSelectedValue('');
    } catch (error) {
      console.error("Error saving data:", error);
      alert("There was an error saving your data.");
    }
  };

  // Handler for radio button change
  const handleTextChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if(parseInt(event.target.value) <50000 ){
      setValid(false);
    }else{
      setValid(true);
      }
    setEnterAmount(event.target.value);
  };

  useEffect(() => {
    if (childValue) {
      setIsOpen(false);
    }
  }, [childValue]);

  return (
    <div className="">
      <div className="wrapper home-wrapper">
        <Header />
        {!!encryptedId && (
          <>
            <TodaysRoundsSection
              gameId={gameId}
              encryptedId={encryptedId}
              bigWinnerList={bigWinnerList}
            />
            <RecentResultSection
              encryptedId={encryptedId}
              lastWingPlanetList={lastWingPlanetList}
            />
          </>
        )}
        <div className="planet-container">
          {showPlanets && (
            <PlanetsSection
              childValue={childValue}
              planets={planet}
              togglePopup={togglePopup}
            />
          )}
          <GameTimer
            onValueChange={handleChildValue}
            parantGameId={gameId}
            onVisible={true}
            onSavedTimes={savedTime}
            onCheckAlreadyStartGame={handleAlreadyGameStart}
            onEndGameValue={handleEndGameDetail}
            onWingAmount={handleWingAmount}
            onEndGameFlage={handleEndGameFlag}
            onUserBetOnPlants={handleUserBits}
            userId={userId}
            setShowPlanets={setShowPlanets}
          />
        </div>
        <CurrentBetSection betOnPlanet={betOnPlanet} betAmount={betAmount} />
        <Footer
          accountBal={accountBal}
          todayBonus={todayBonus?.bouns_amount ? todayBonus?.bouns_amount : 0}
        />
        {/* Popup Modal */}
        {isOpen && (
          <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
            <BetForm
              handleSubmit={handleSubmit}
              handleTextChange={handleTextChange}
              handleRadioChange={handleRadioChange}
              enterAmount={enterAmount}
              selectedValue={selectedValue}
            />
          </Modal>
        )}
        {/* Popup Modal */}
        {endGame && (
          <Modal isOpen={endGame}>
            <ResultModalContent
              endGameDetail={endGameDetail}
              userBetOnPlants={userBetOnPlants}
              winAmount={winAmount}
            />
          </Modal>
        )}
      </div>
      <SweetAlert2
        {...swalProps}
        didClose={() => {
          setSwalProps({});
        }}
      />
      <ToastContainer />
    </div>
  );
};

export default HomePage;
