import CryptoJS from 'crypto-js';

// Example usage
const secretKey = "lWwVPaVS81hmJedS"; // 16-character key
 
// Encrypt function
export const encrypt = (data:string):string=> {
    // Encrypt the data using AES-128-ECB
    const encrypted = CryptoJS.AES.encrypt(
        data, // Plain text data
        CryptoJS.enc.Utf8.parse(secretKey), // Convert key to WordArray
        {
            mode: CryptoJS.mode.ECB,
            padding: CryptoJS.pad.Pkcs7,
        }
    );

    // Convert encrypted data to Base64 URL-safe string
    return encrypted.toString(CryptoJS.format.OpenSSL).replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
}

// Decrypt function
export const decrypt = (encryptedData:string):string=> {
    // Reverse the Base64 URL-safe encoding
    const base64 = encryptedData.replace(/-/g, '+').replace(/_/g, '/');

    // Decrypt the data using AES-128-ECB
    const decrypted = CryptoJS.AES.decrypt(
        base64, // Encrypted Base64 string
        CryptoJS.enc.Utf8.parse(secretKey), // Convert key to WordArray
        {
            mode: CryptoJS.mode.ECB,
            padding: CryptoJS.pad.Pkcs7,
        }
    );

    // Convert decrypted data to a UTF-8 string
    return decrypted.toString(CryptoJS.enc.Utf8);
  }