import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { getAllMultipliers, getUserBitHistory } from "../api/api";
import { decrypt } from '../utils/cryptoUtils';
import CryptoJS from "crypto-js";
type Params = {
  id: string;
};

interface Item {
  created_at: string;
  end_time: string;
  id: number;
  planet_name: string;
  start_time: string;
  status: string;
  total_amount_on_1: number;
  total_amount_on_2: number;
  total_amount_on_3: number;
  total_amount_on_4: number;
  total_amount_on_5: number;
  total_amount_on_6: number;
  total_amount_on_7: number;
  total_amount_on_8: number;
  total_bet_amount: number;
  total_no_of_users: number;
  updated_at: string;
  win_planet: string;
}
const HistoryPage: React.FC = (Params) => {
  const { id: encryptedId } = useParams<Params>();
  let decodedId: string | null = null;
  try {
    decodedId = encryptedId ? atob(encryptedId) : null; // Decode the encrypted ID
  } catch (error) {
    console.error("Failed to decode ID:", error);
    decodedId = null;
  }
  const [userId, setUserId] = useState<number>(0);
  const [planet, setPlanet] = useState([]);
  const [historyDetailView, setHistoryDetailView] = useState<boolean>(false);
  const [historyList, setHistoryList] = useState<Item[]>([]);
  const [historyDetail, setHistoryDetail] = useState<any>([]);

  const [page, setPage] = useState<number>(1); // Current page number
  const [loading, setLoading] = useState<boolean>(false); // Loading state
  const [hasMore, setHasMore] = useState<boolean>(true); // Check if more data is available
  const [noOFPages, setNoOFPages] = useState<number>(0);

  useEffect(() => {
    if (!decodedId) return;
    if (!encryptedId) return;
    if (encryptedId) {
      const decrypted = decrypt(encryptedId);	
      let id = parseInt(decrypted);
      setUserId(id);
      
    }
    fetchData();
  }, [encryptedId]);

  useEffect(() => {
    fetchHistoryList(page);
  }, [page]);
  async function fetchData() {
    const data = await getAllMultipliers();
    if (data.status) {
      setPlanet(data?.details);
    } else {
      setPlanet([]);
    }
  }
  /**
   * Get Fetch Hostory list
   */
  async function fetchHistoryList(page: number) {
    const param = {
      userId:userId,
    };
    setLoading(true);
    const data = await getUserBitHistory(page,userId);
    if (data.status) {
      setNoOFPages(data?.details?.total_pages);
      setHistoryList((prevItems) => [...prevItems, ...data?.details?.data]);
    } else {
      setHasMore(false); // No more data
    }
    setLoading(false);
  }

  const togglePopup = (history: any) => {
    setHistoryDetail(history);
    setHistoryDetailView(!historyDetailView);
  };
  const loadMoredata = () => {
    if (noOFPages > page) {
      setPage((prevPage) => prevPage + 1); // Increment the page
    } else {
      setHasMore(false);
    }
  };

  return (
    <div className="">
      <div className="wrapper">
        <div className="fixed-layout fixed">
          <div className="top-header">
            <Link to={`/${encryptedId}`} className="btn-top">
              <svg
                width="21"
                height="16"
                viewBox="0 0 21 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M3.825 9L9.425 14.6L8 16L0 8L8 0L9.425 1.4L3.825 7H21V9H3.825Z"
                  fill="currentColor"
                />
              </svg>
            </Link>
            <Link to={`/${encryptedId}`} className="btn-top">
              <svg
                width="12"
                height="12"
                viewBox="0 0 12 12"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M5.97055 7.41421L1.72791 11.6569L0.313701 10.2426L4.55634 6L0.313701 1.75736L1.72791 0.343146L5.97055 4.58579L10.2132 0.343146L11.6274 1.75736L7.38477 6L11.6274 10.2426L10.2132 11.6569L5.97055 7.41421Z"
                  fill="currentColor"
                />
              </svg>
            </Link>
          </div>
          <div className="layout-center">
            <h2>Round</h2>
            <ul className="layout-menu">
              {historyList.length > 0 &&
                historyList.map((value, index) => (
                  <li key={index}>
                    <a onClick={() => togglePopup(value)}>
                      {value["id"]}
                      <span>
                        <svg
                          width="8"
                          height="12"
                          viewBox="0 0 8 12"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M4.6 6L0 1.4L1.4 0L7.4 6L1.4 12L0 10.6L4.6 6Z"
                            fill="currentColor"
                          />
                        </svg>
                      </span>
                      <span
                        className="icon"
                        style={{
                          left: `${parseInt(value["win_planet"]) * 10 + 10}%`,
                        }}
                      >
                        <img
                          src={require("../assets/images/star-img.png")}
                          alt=""
                        />
                      </span>
                    </a>
                  </li>
                ))}
              {hasMore && (
                <li>
                  <a onClick={loadMoredata}>
                    <div className="more-item">More item</div>
                  </a>
                </li>
              )}
            </ul>
            {loading && <p>Loading...</p>}
            {!hasMore && <p>No more items to display</p>}
            <ul className="layout-bottom">
              {planet.length > 0 &&
                planet.map((value) => (
                  <li>
                    <div className="img">
                      <img
                        src={require(
                          `../assets/images/planets/planet__${value["id"]}.webp`,
                        )}
                        alt=""
                      />
                    </div>
                    <p>x{value["multiplier"]}</p>
                  </li>
                ))}
            </ul>
          </div>
        </div>
        {historyDetailView && (
          <div className="modal fixed-modal">
            <div className="modal-content">
              <span
                className="close-button"
                id="closeModal"
                onClick={() => togglePopup({})}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                >
                  <line x1="18" y1="6" x2="6" y2="18"></line>
                  <line x1="6" y1="6" x2="18" y2="18"></line>
                </svg>
              </span>
              <div className="fixed-coin">
                <span>
                  <img src={require("../assets/images/icon.png")} alt="" />
                </span>
                <h3>
                  {historyDetail["win_amount"] != null
                    ? historyDetail["win_amount"]
                    : 0}
                </h3>
              </div>
              <h4>{historyDetail["id"]} Round Bonus</h4>
              <div className="modal-list">
                <ul>
                  {planet.length > 0 &&
                    planet.map((value, index) => (
                      <li>
                        {index + 1 == historyDetail["win_planet"] && (
                          <div className="select-star">
                            <img
                              src={require("../assets/images/star-img.png")}
                              alt=""
                            />
                          </div>
                        )}
                        <div className="planet-img">
                          <img
                            src={require(
                              `../assets/images/planets/planet__${value["id"]}.webp`,
                            )}
                            alt=""
                          />
                        </div>
                        <div className="planet-coin">
                          <div className="coin-img">
                            <img
                              src={require("../assets/images/icon.png")}
                              alt=""
                            />
                          </div>
                          <p>
                            {historyDetail["total_amount_on_" + (index + 1)] !=
                            null
                              ? historyDetail["total_amount_on_" + (index + 1)]
                              : 0}
                          </p>
                        </div>
                      </li>
                    ))}
                </ul>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
export default HistoryPage;
