import React from "react";
import { Link } from "react-router-dom";

import styles from "./index.module.css";

interface TodaysRoundsSectionProps {
  gameId: number;
  encryptedId: string;
  bigWinnerList: BigWinnerItem[];
}

const TodaysRoundsSection = ({
  gameId,
  encryptedId,
  bigWinnerList,
}: TodaysRoundsSectionProps) => {
  return (
    <div className={styles.round}>
      <p className={styles.text}>Today's {gameId} Round</p>
      <Link to={`/${encryptedId}/bigsWinn`}>
        <ul>
          {!!bigWinnerList?.length &&
            bigWinnerList.map(
              (planet, index) =>
                index < 5 && (
                  <li key={`big-winner-item-${index}`}>
                    {!!planet.image ? (
                      <img
                        src={planet.image}
                        alt={`Avatar-${index}`}
                        width={40}
                        height={40}
                      />
                    ) : (
                      <img
                        src={require("../../assets/images/avatar.jpg")}
                        alt={`Avatar-${index}`}
                        width={40}
                        height={40}
                      />
                    )}
                  </li>
                ),
            )}
          <li>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="3"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <line x1="12" y1="5" x2="12" y2="19"></line>
              <line x1="5" y1="12" x2="19" y2="12"></line>
            </svg>
          </li>
        </ul>
      </Link>
    </div>
  );
};

export default TodaysRoundsSection;
