import React from "react";

import styles from "./index.module.css";

interface BetFormProps {
  handleSubmit: (event: React.FormEvent) => Promise<void>;
  handleTextChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  handleRadioChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  enterAmount: string;
  selectedValue: string;
}

const BetForm = ({
  handleSubmit,
  enterAmount,
  handleTextChange,
  selectedValue,
  handleRadioChange,
}: BetFormProps) => {
  return (
    <form onSubmit={handleSubmit}>
      <h3 className={styles.title}>How many coins do you want to bet?</h3>
      <input
        type="text"
        className={styles.input}
        placeholder="Enter Coins"
        value={enterAmount}
        onChange={handleTextChange}
      />

      <ul className={styles.coinHolder}>
        <li>
          <input
            type="checkbox"
            id="coins_10"
            name="coins"
            value="10"
            checked={selectedValue === "10"}
            onChange={handleRadioChange}
          />
          <label htmlFor="coins_10">
            <span className={styles.icon}>
              <img src={require("../../assets/images/icon.png")} alt="" />
            </span>
            10
          </label>
        </li>
        <li>
          <input
            type="checkbox"
            id="coins_50"
            name="coins"
            value="50"
            checked={selectedValue === "50"}
            onChange={handleRadioChange}
          />
          <label htmlFor="coins_50">
            <span className={styles.icon}>
              <img src={require("../../assets/images/icon.png")} alt="" />
            </span>
            50
          </label>
        </li>
        <li>
          <input
            type="checkbox"
            id="coins_100"
            name="coins"
            value="100"
            checked={selectedValue === "100"}
            onChange={handleRadioChange}
          />
          <label htmlFor="coins_100">
            <span className={styles.icon}>
              <img src={require("../../assets/images/icon.png")} alt="" />
            </span>
            100
          </label>
        </li>
        <li>
          <input
            type="checkbox"
            id="coins_1000"
            name="coins"
            value="1000"
            checked={selectedValue === "1000"}
            onChange={handleRadioChange}
          />
          <label htmlFor="coins_1000">
            <span className={styles.icon}>
              <img src={require("../../assets/images/icon.png")} alt="" />
            </span>
            1000
          </label>
        </li>
      </ul>
      <button id="modalOkButton" className={styles.button}>
        BET
      </button>
    </form>
  );
};

export default BetForm;
