import React from "react";

import styles from "./index.module.css";

interface BetOnPlanets {
  planet_id: number;
  planet_image: string;
  planet_name: string;
}

interface CurrenBetSectionProps {
  betOnPlanet: BetOnPlanets[];
  betAmount: number;
}

const CurrentBetSection = ({
  betAmount,
  betOnPlanet,
}: CurrenBetSectionProps) => {
  return (
    <div className={styles.currentBetView}>
      <div className={styles.currentBet}>
        Bet On:
        <div className={styles.planetsHolder}>
          {betOnPlanet.length > 0 &&
            betOnPlanet.map((planet, index) => (
              <img
                key={`current-bet-planet-item-${index}`}
                src={require(
                  `../../assets/images/planets/planet__${planet.planet_id}.webp`,
                )}
                alt={`Bet On Planet ${index}`}
                width={40}
                height={40}
                className={styles.planetImage}
              />
            ))}
        </div>
      </div>
      <div className={styles.betAmountHolder}>
        Bet Amount:
        <span className="icon">
          <img
            src={require("../../assets/images/icon.png")}
            alt="Coin Icon"
            width={20}
            height={20}
            className={styles.coin}
          />
        </span>
        <span className={styles.betAmount}>{betAmount}</span>
      </div>
    </div>
  );
};

export default CurrentBetSection;
