import React from "react";
import { Link } from "react-router-dom";

import styles from "./index.module.css";

interface RecentResultSectionProps {
  encryptedId: string;
  lastWingPlanetList: LastWingPlanets[];
}

const RecentResultSection = ({
  encryptedId,
  lastWingPlanetList,
}: RecentResultSectionProps) => {
  return (
    <div className={styles.resultSection}>
      <p className={styles.text}>Result</p>
      <Link to={`/${encryptedId}/history`} className={styles.resultHolder}>
        {!!lastWingPlanetList?.length &&
          lastWingPlanetList.map((planet, index) => (
            <img
              key={`recent-result-planet-${index}`}
              src={require(
                `../../assets/images/planets/planet__${planet.planet_id}.webp`,
              )}
              width={48}
              alt={`Rescent Result Planet ${index}`}
              className={styles.image}
            />
          ))}
      </Link>
    </div>
  );
};

export default RecentResultSection;
