import React from "react";

import styles from "./index.module.css";
import { shuffleArray } from "../../utils";

interface Planet {
  id: number;
  multiplier: number;
}

interface PlanetsSectionProps {
  childValue: boolean;
  planets: Planet[];
  togglePopup: (planetId: number) => void;
}

const AnimationOffsets = [
  {
    xOffsetInitial: "-50%",
    yOffsetInitial: "0",
    xOffset: "-50%",
    yOffset: "min(50vw, 180px)",
  },
  {
    xOffsetInitial: "-50%",
    yOffsetInitial: "-50%",
    xOffset: "max(-50vw, -180px)",
    yOffset: "min(25vw, 90px)",
  },
  {
    xOffsetInitial: "0",
    yOffsetInitial: "-50%",
    xOffset: "max(-50vw, -180px)",
    yOffset: "-50%",
  },
  {
    xOffsetInitial: "-50%",
    yOffsetInitial: "50%",
    xOffset: "max(-50vw, -180px)",
    yOffset: "max(-25vw, -90px)",
  },
  {
    xOffsetInitial: "-50%",
    yOffsetInitial: "0",
    xOffset: "-50%",
    yOffset: "max(-50vw, -180px)",
  },
  {
    xOffsetInitial: "50%",
    yOffsetInitial: "50%",
    xOffset: "min(50vw, 180px)",
    yOffset: "max(-25vw, -90px)",
  },
  {
    xOffsetInitial: "0",
    yOffsetInitial: "-50%",
    xOffset: "min(50vw, 180px)",
    yOffset: "-50%",
  },
  {
    xOffsetInitial: "-50%",
    yOffsetInitial: "-50%",
    xOffset: "min(50vw, 140px)",
    yOffset: "min(25vw, 90px)",
  },
];

const AnimationDelays = [1.75, 2, 2.25, 2.50, 2.75, 3, 3.25, 3.50];

const Planets = ({ childValue, planets, togglePopup }: PlanetsSectionProps) => {
  const shuffledAnimationDelay = shuffleArray(AnimationDelays);
  return (
    <div className={`${styles.container} `}>
      <div
        className={`${styles.planetList} ${
          childValue ? styles.planetListAnimation : ""
        }`}
      >
        {planets.length > 0 &&
          planets.map((planet, index) => {
            return (
              <div
                  key={`game-planets-${index}`}
                className={`${styles.planet} ${
                  childValue ? styles.planetAnimation : ""
                }`}
                style={
                  {
                    animationDelay: `${shuffledAnimationDelay[index]}s`,
                    "--initial-x": AnimationOffsets[index].xOffsetInitial,
                    "--initial-y": AnimationOffsets[index].yOffsetInitial,
                    "--final-x": AnimationOffsets[index].xOffset,
                    "--final-y": AnimationOffsets[index].yOffset,
                  } as React.CSSProperties
                }
                onClick={() => togglePopup(planet.id)}
              >
                <img
                  src={require(`../../assets/images/planets/planets_bg.webp`)}
                  alt="Planet ing"
                  width={88}
                  height={88}
                  className={`${styles.planetBg} ${!childValue && styles.showPlanetBg}`}
                />
                <img
                  src={require(
                    `../../assets/images/planets/planet__${planet.id}.webp`,
                  )}
                  alt="Planet Image"
                  width={88}
                  height={88}
                  className={styles.image}
                />
                {!childValue && (
                  <>
                    <p className={styles.multiplier}>
                      {planet.multiplier} Times
                    </p>
                    {/*<img*/}
                    {/*  src={require("../../assets/images/icon.png")}*/}
                    {/*  alt="Coin"*/}
                    {/*  width={20}*/}
                    {/*  height={20}*/}
                    {/*  className={styles.coin}*/}
                    {/*/>*/}
                  </>
                )}
              </div>
            );
          })}
      </div>
      <img
        src={require(`../../assets/images/wave-bg.webp`)}
        alt="Planet Image"
        width={88}
        height={88}
        className={`${styles.galaxy} ${
          childValue ? styles.galaxyAnimation : ""
        }`}
      />
    </div>
  );
};

export default Planets;
