import React from "react";

import styles from "./index.module.css";

const FooterItem = ({ title, amount }: { title: string; amount: number }) => {
  return (
    <div className={styles.footerItem}>
      {title}
      <div className={styles.amount}>
        <img
          src={require("../../assets/images/icon.png")}
          alt="Coin"
          width={20}
          height={20}
          className={styles.coin}
        />
        {amount}
      </div>
    </div>
  );
};

const Footer = ({
  accountBal,
  todayBonus,
}: {
  accountBal: number;
  todayBonus: number;
}) => {
  return (
    <footer className={styles.footer}>
      <FooterItem title={"Account Balance"} amount={accountBal}></FooterItem>
      <FooterItem title={"Today's Bonus"} amount={todayBonus}></FooterItem>
    </footer>
  );
};

export default Footer;
