import React, { useRef } from "react";

import styles from "./index.module.css";

interface WinnerModel {
  name: string;
  winAmount: number;
  avatar: string;
}

interface GameDetailModel {
  created_at: string;
  end_time: string;
  id: number;
  planet_name: string;
  start_time: string;
  status: string;
  total_amount_on_1: number;
  total_amount_on_2: number;
  total_amount_on_3: number;
  total_amount_on_4: number;
  total_amount_on_5: number;
  total_amount_on_6: number;
  total_amount_on_7: number;
  total_amount_on_8: number;
  total_bet_amount: number;
  total_no_of_users: number;
  updated_at: string;
  win_planet: string;
  planet_url?: string;
  topWinner?: WinnerModel[];
}

interface ResultModalContentProps {
  endGameDetail: GameDetailModel;
  userBetOnPlants: any[];
  winAmount: number;
}

const Winner = ({ data, index }: { data: WinnerModel; index: number }) => {
  return (
    <div className={styles.winner}>
      <img
        src={require("../../assets/images/winer_first.png")}
        alt="Winner Image"
        width={60}
        height={60}
        className={styles.winnerImage}
      />
      <img
        src={require(`../../assets/images/medal_${index + 1}.webp`)}
        alt="Winner Medal"
        width={60}
        height={60}
        className={styles.winnerMedal}
      />
      <p className={styles.name}>{data.name}</p>
      <div className={styles.amountHolder}>
        <img
          src={require(`../../assets/images/coins.png`)}
          alt="Coin"
          width={20}
          height={20}
          className={styles.coin}
        />
        <span className={styles.amount}>{data.winAmount}</span>
      </div>
    </div>
  );
};

const WinnersContainer = ({
  winners,
  className,
}: {
  winners: WinnerModel[];
  className?: string;
}) => {
  return (
    <div className={`${className}`}>
      <div className={styles.winnerTitle}>Winners of this round</div>
      <div className={styles.winnersList}>
        <img
          src={require(`../../assets/images/winners_stage.webp`)}
          alt="Winner Stage"
          width={60}
          height={60}
          className={styles.winnersStage}
        />
        {winners.map((winner, idx) => (
          <Winner key={`game-winner-${idx}`} data={winner} index={idx} />
        ))}
      </div>
    </div>
  );
};

const ResultModalContent = ({
  endGameDetail,
  userBetOnPlants,
  winAmount,
}: ResultModalContentProps) => {
  const winPlanetRef = useRef(
    endGameDetail?.win_planet || userBetOnPlants[0] || 1,
  );
  const userResultRef = useRef(
    Object.keys(endGameDetail).length > 0 &&
      userBetOnPlants.includes(endGameDetail.win_planet)
      ? "win"
      : "lost",
  );

  return (
    <div className={styles.container}>
      <img
        src={require(
          `../../assets/images/planets/planet__${winPlanetRef.current}.webp`,
        )}
        alt="Planet Image"
        width={60}
        height={60}
        className={styles.planet}
      />
      {userResultRef?.current === "win" ? (
        <div className={styles.winView}>
          <img
            className={styles.confetti}
            src={require("../../assets/images/confetti.webp")}
            alt={"confetti"}
            width={300}
            height={300}
          />
          <div className={styles.winContent}>
            <h3 className={styles.winTitle}>Congratulations!</h3>
            <p className={styles.winText}>You just received</p>
            <div className={styles.winAmount}>
              <img
                className={styles.coinImg}
                src={require("../../assets/images/coins.png")}
                alt={"Coin"}
                width={20}
                height={20}
              />
              <span className={styles.winCoins}>{winAmount}</span>
            </div>
            <p className={styles.winText}>Coins</p>
          </div>
        </div>
      ) : (
        <div className={styles.missView}>
          <span className={styles.emoji}></span>
          <div className={styles.missedContent}>
            <h3 className={styles.missedTitle}>You missed the big prize!</h3>
            <p className={styles.missedText}>
              With no fear, the bicycle becomes a motorcycle.
            </p>
          </div>
        </div>
      )}
      {endGameDetail?.topWinner?.length && (
        <WinnersContainer
          winners={endGameDetail?.topWinner}
          className={
            Object.keys(endGameDetail).length > 0 &&
            userBetOnPlants.includes(endGameDetail.win_planet)
              ? styles.winnerContainer
              : ""
          }
        />
      )}
    </div>
  );
};

export default ResultModalContent;
