import React, { useEffect, useState } from "react";
import "react-circular-progressbar/dist/styles.css";

import { newGameStart, endGameApi } from "../../api/api";

import styles from "./index.module.css";

interface Props {
  onValueChange: (value: boolean) => void;
  onCheckAlreadyStartGame: (value: number) => void;
  onEndGameValue: (value: any) => void;
  onEndGameFlage: (value: boolean) => void;
  parantGameId: number;
  onVisible: boolean;
  onSavedTimes: any;
  onUserBetOnPlants: (value: any) => void;
  userId: any;
  onWingAmount: (value: number) => void;
  setShowPlanets: (showPlanet: boolean) => void;
}

const GameTimer: React.FC<Props> = ({
  onValueChange,
  onCheckAlreadyStartGame,
  onEndGameValue,
  onEndGameFlage,
  parantGameId,
  onVisible,
  onSavedTimes,
  onUserBetOnPlants,
  userId,
  onWingAmount,
  setShowPlanets,
}) => {
  const [gameMin, setGameMin] = useState<number>(32);
  const [isActive, setIsActive] = useState<boolean>(true);
  const [timeLeft, setTimeLeft] = useState<number>(0);
  const [timeShow, setTimeShow] = useState<boolean>(true);
  const sendValueToParent = (endGame: boolean) => {
    onValueChange(endGame);
  };

  const setValueAlreadyStartGame = (endGame: number) => {
    onCheckAlreadyStartGame(endGame);
  };
  const setUserBetOnPlants = (resetUserBit: any) => {
    onUserBetOnPlants(resetUserBit);
  };
  const setValueEndGame = (endGameDetail: any) => {
    onEndGameValue(endGameDetail);
    onEndGameFlage(true);
  };
  const setUserWingAmount = (value: number) => {
    onWingAmount(value);
  };

  useEffect(() => {
    const socket = new WebSocket("ws://treasure.rockstarstudioz.com:8080");

    // When a message is received from the server, update the timer
    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);
      setTimeLeft(32 - data.seconds);
      formatTime(32 - data.seconds);
    };

    // Clean up WebSocket connection on component unmount
    return () => {
      socket.close();
    };
  }, [parantGameId]);

  const progress = (100 * (timeLeft - 7)) / 1 / gameMin;
  const resetTimer = () => {
    const initialTime = gameMin * 1; // 30 minutes in seconds
    setTimeLeft(initialTime);
    setIsActive(true);
    localStorage.setItem("timeLeft", initialTime.toString()); // Save initial time
    startNewGame();
  };

  const formatTime = (seconds: number) => {
    if (seconds == 32) {
      setShowPlanets(true);
      sendValueToParent(false);
      resetTimer();
      onEndGameValue({});
      setTimeShow(true);
      // onEndGameFlage(false);
    } else if (seconds == 7) {
      setTimeShow(false);
      sendValueToParent(true);
    } else if (seconds == 2) {
      setShowPlanets(false);
      sendValueToParent(false);
      endGame();
      setTimeShow(false);
    }
  };
  async function endGame() {
    const param = {
      userId: userId,
      gameId: parantGameId,
    };
    const data = await endGameApi(param);
    if (data.status == "1") {
      setValueEndGame(data?.details);
      setUserWingAmount(data?.win_amount);
    }
  }
  async function startNewGame() {
    const data = await newGameStart();
    setValueAlreadyStartGame(data.game_id);
    setUserBetOnPlants([]);
  }

  return (
    <div className={`${styles.container} ${!timeShow && styles.hideContainer}`}>
      <div className={`${styles.content}`}>
        <img
          src={require(`../../assets/images/timer_bg.webp`)}
          alt="Timer Background"
          width={100}
          height={100}
          className={styles.bgImg}
        />
        <p className={`${styles.time}  ${timeShow ? "active" : "inactive"}`}>
          {timeLeft - 7 > 0 ? timeLeft - 7 : 0}
        </p>
      </div>
    </div>
  );
};

export default GameTimer;
